import React,{useState} from 'react';
import img1 from './img1.jpg';
import img2 from './img2.jpg';
import './App.css';

function App() {

  let cpnt = [<Cpnt1 />, <Cpnt2 />];
  let [idx,setIdx] = useState(0);

  function aa(e){
    let num = e.target.dataset.num;
    let aTag = document.querySelectorAll('.test a');

    aTag.forEach(function(el,k){
      if(num ==k){
        el.classList.add("active");
      }else{
        el.classList.remove("active");
      }
    });

    setIdx(num);
  }



  return (
    <>
      <header className="test">
        <nav>
          <a onClick={aa} data-num="0" className="active">별이</a>
          <a onClick={aa} data-num="1">쫑쫑이</a>
        </nav>
      </header>
      <main>
        <div> {cpnt[idx]} </div>
      </main>
    </>
  );
}

function Cpnt1(){
  return( 
    <div>
      <p>별이</p>
      <img src={img1} />
    </div>
    
  );
}

function Cpnt2(){
  return( 
    <div>
      <p>쫑쫑이</p>
      <img src={img2} />
    </div>
   );
}

export default App;
